import React from 'react';
import { Box } from '../Box';


export const Text = ({ children, as = 'p', ...rest }) => <Box {...rest} as={as} __css={{
    fontSize: 'inherit',
    fontWeight: 'inherit',
    lineHeight: 'inherit',
    ...rest.__css
}}>
    {children}
</Box>

export const Paragraph = ({
    children,
    ...rest
}) => {

    return (
        <Text __css={{
            fontSize: "paragraph",
            lineHeight: "paragraph",
            mb: "xsmall",
        }}
            {...rest}>
            {children}
        </Text>
    );
}

export const Heading = ({
    children,
    level = '1',
    ...rest
}) => {

    return (
        <Text
            __css={{
                fontSize: `heading${level}`,
                lineHeight: `heading${level}`,
                mb: "small",
            }}
            {...rest}
            as={`h${level}`}>{children}</Text>
    );
}

export default Text;


